body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-container {
  z-index: 10;
  position: absolute;
  color: white;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  bottom: -200px;
  top: 0px;
  left: 0px;
  right: 0px;
  display: flex;
  /* align-items: center; */
  justify-content: center;
}

.grid {
  background-color: red;
  margin-top: 150px;
  width: 1200px;
  height: 800px;
  display: flex;
  flex-direction: column;
}

.row1, .row2, .row3 {
  display: flex;
}

.aboutme {
  flex: 2;
  height: 250px;
  background-color: blue;
  margin: 8px;
  border-radius: 20px;
}

.map {
  flex: 1;
  height: 250px;
  background-color: blue;
  margin: 8px;
  border-radius: 20px;
}

.resume {
  flex: 1;
  height: 250px;
  background-color: blue;
  margin: 8px;
  border-radius: 20px;
}

.scroller {
  flex: 1;
  height: 250px;
  background-color: blue;
  margin: 8px;
  border-radius: 20px;
}

.github {
  width: 150px;
  height: 118px;
  background-color: blue;
  margin: 8px;
  margin-bottom: 14px;
  border-radius: 20px;
}

.linkedin {
  width: 150px;
  height: 118px;
  background-color: blue;
  margin: 8px;
  border-radius: 20px;
}

.project1, .project2 {
  flex: 1;
  height: 250px;
  background-color: blue;
  margin: 8px;
  border-radius: 20px;
}
